<template>
  <div class="manualManage">
    <div class="header">
      <div class="selectNav">
        <el-select
          size="small"
          class="input_td"
          v-model="queryForm.dsj"
          clearable
          placeholder="请选择地市局"
          @change="dsjChange"
        >
          <el-option
            v-for="item in dsjOptions"
            :key="item.ORG_CODE"
            :label="item.ORG_NAME"
            :value="item.ORG_CODE"
          ></el-option>
        </el-select>
        <el-select
          v-model="queryForm.xqj"
          clearable
          placeholder="请选择县区局"
          class="input_td"
          size="small"
          @change="qxjChange"
        >
          <el-option
            v-for="item in xqjOptions"
            :key="item.ORG_CODE"
            :label="item.ORG_NAME"
            :value="item.ORG_CODE"
          ></el-option>
        </el-select>
        <el-select
          size="small"
          class="input_td"
          v-model="queryForm.gds"
          clearable
          placeholder="请选择供电所"
        >
          <el-option
            v-for="item in gdsOptions"
            :key="item.ORG_CODE"
            :label="item.ORG_NAME"
            :value="item.ORG_CODE"
          ></el-option>
        </el-select>
        <el-input
          class="input_300"
          placeholder="请输入用户编号"
          size="small"
          v-model="queryForm.yhbh"
          clearable
        ></el-input>
        <el-button type="primary" size="small" icon="el-icon-search" @click="getTableData">查询</el-button>
        <el-upload
          class="upload"
          action="/portal/strategy/tmNpmisTdmgjhgjkhxxT/uploadFileSecond"
          :on-success="handleSuccess"
          multiple
          :limit="1"
          accept=".xls, .xlsx"
          :before-upload="beforeUpload"
          :show-file-list="false"
        >
          <el-button size="small" type="primary">上传文件</el-button>
        </el-upload>
        <el-button type="primary" size="small" icon="el-icon-search" @click="submitMark">标记提交</el-button>
      </div>
    </div>
    <div class="content">
      <div class="title">停电敏感客户第2次通知客户标签标记</div>
      <div class="tableTd">
        <el-table
          v-loading="loading"
          border
          :data=" tableData"
          style="width: 100%"
          :row-class-name="tableRowClassName"
        >
          <el-table-column label="序号" align="center" type="index" width="70"></el-table-column>
          <el-table-column prop="yhbh" label="用户编号"></el-table-column>
          <el-table-column prop="yhmc" label="用户名称"></el-table-column>
          <el-table-column prop="yddz" label="用电地址"></el-table-column>
          <el-table-column prop="dsj" label="地市局"></el-table-column>
          <el-table-column prop="xqj" label="县区局"></el-table-column>
          <el-table-column prop="gds" label="供电所"></el-table-column>
          <el-table-column prop="updateTime" label="标签更新时间"></el-table-column>
          <el-table-column align="center" width="200" label="选择至停电敏感客户标签标记清单">
            <template slot-scope="scope">
              <!-- <el-link type="danger">删除{{scope.name}}</el-link> -->
              <el-checkbox
                v-model="scope.row.bindTagB"
                :disabled="scope.row.bindTagStatus"
                @change="bindTagBChange(scope.row)"
              >是否标记</el-checkbox>
            </template>
          </el-table-column>
        </el-table>
        <div class="block">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="this.queryForm.current"
            :page-sizes="[10, 20,30,40]"
            :page-size="this.queryForm.size"
            layout="total, sizes, prev, pager, next, jumper"
            :total="this.queryForm.total"
          ></el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Interface1 from '@/base/mixin/power-sensitive-users';
import Interface2 from '@/base/mixin/common-api';

export default {
  name: "manualManage",
  components: {
  },
  props: {

  },
  mixins: [Interface1, Interface2],

  data() {
    return {
      dsjOptions: [],
      xqjOptions: [],
      gdsOptions: [],
      queryForm: {
        // yhbh: "0313001300620313",
        yhbh: "",
        dsj: "",
        xqj: "",
        gds: "",
        current: 1,
        size: 10,
        total: 0,
      },
      //第2次停电通知算法识别结果清单
      tableData: [],
      tableDataSelect: [],
      loading: false,
    }
  },
  mounted() {
    this.getAreaCode()
    this.getTableData()

  },
  methods: {
    //地市局选择
    dsjChange() {
      this.queryForm.gds = ''
      this.gdsOptions = []
      this.queryForm.xqj = ''
      this.xqjOptions = []
      this.getQxjPowerAreaCode()
    },
    //区县局选择
    qxjChange() {
      this.queryForm.gds = ''
      this.gdsOptions = []
      this.getGdsPowerAreaCode()
    },
    handleSizeChange(val) {
      this.queryForm.size = val
      this.getTableData()
    },
    handleCurrentChange(val) {
      this.queryForm.current = val
      this.getTableData()
    },
    //上传
    beforeUpload(file, fileList) {
      var testmsg = file.name.substring(file.name.lastIndexOf('.') + 1)
      let isType = (testmsg === 'xls' || testmsg === 'xlsx')
      if (!isType) {
        this.$alert(`上传文件只能是xls、xlsx格式!`);
      }
      return isType;
    },
    handleSuccess(file) {
      console.log(file);
      if (file.code === "00000") {
        this.$message.success('上传成功')
        this.tableData = [...file.data.records]
        this.queryForm.total = file.data.total
        this.tableData.forEach(e => {
          e.bindTagB = e.bindTag === '1' ? true : false
          e.bindTagStatus = e.bindTag === '1' ? true : false
        })
      }
    },
    // 表格样式
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 === 0) {
        return 'success-row';
      }
      return '';
    },
    // 改变是否标注
    bindTagBChange(row) {
      let tableData = [...this.tableData]
      this.tableData = []
      this.tableData = [...tableData]
      if (this.tableDataSelect.length !== 0) {
        if (this.tableDataSelect.indexOf(row.yhbh) === -1) {
          this.tableDataSelect.push(row.yhbh)
        } else {
          this.tableDataSelect.splice(this.tableDataSelect.indexOf(row.yhbh), 1)
        }
      } else {
        this.tableDataSelect.push(row.yhbh)

      }

      console.log(this.tableData, 'this.tableData');
      console.log(this.tableDataSelect, 'this.tableDataSelect');
    },
    // 标注提交
    submitMark() {
      let yhbhs = []
      this.tableData.filter(e => e.bindTagB).forEach(e => {
        yhbhs.push(e.yhbh)
      })
      this.markSecondeTimeTag({ yhbhs }).then(res => {
        if (res) {
          this.$message.success('标记成功')
          this.getTableData()
        }
      })
    },
    //获取供电单位编码--地市局
    getAreaCode() {
      this.getPowerAreaCode({
        "orgLev": "2",//2==供电单位==地市局，3==县区局局，4===供电所
        // "orgCode":"0306" //上一级编码
      }).then(res => {
        this.dsjOptions = res
      })
    },
    //获取区县局--区县局
    getQxjPowerAreaCode() {
      this.getPowerAreaCode({
        "orgLev": "3",//2==供电单位==地市局，3==县区局局，4===供电所
        "orgCode": this.queryForm.dsj //上一级编码
      }).then(res => {
        this.xqjOptions = res
      })
    },
    //获取供电所
    getGdsPowerAreaCode() {
      this.getPowerAreaCode({
        "orgLev": "4",//2==供电单位==地市局，3==县区局局，4===供电所
        "orgCode": this.queryForm.xqj //上一级编码
      }).then(res => {
        this.gdsOptions = res
      })
    },
    // 
    getTableData() {
      this.getSecondTimeTagUser(this.queryForm).then(res => {
        this.tableData = [...res.records]
        this.queryForm.total = res.total
        this.tableData.forEach(e => {
          e.bindTagB = e.bindTag === '1' ? true : false
          e.bindTagStatus = e.bindTag === '1' ? true : false
        })
      })
    },
  }
}
</script>

<style scoped lang="scss">
.manualManage {
  .input_td {
    width: 200px;
    margin-right: 20px;
  }
  .input_300 {
    width: 300px;
    margin-right: 20px;
  }
  .header {
    line-height: 30px;
    .selectNav {
      height: 30px;
      margin-bottom: 16px;
    }
    .upload {
      display: inline-block;
      margin: 0 10px;
    }
  }
  .content {
    .title {
      padding-left: 18px;
      border-left: 6px solid #1a84f4;
      font-weight: bold;
      margin-bottom: 10px;
    }
    .tableTd {
      .block {
        text-align: center;
        margin-top: 10px;
      }
    }
  }
}
</style>