<template>
  <div class="probablyComplaintUser">
    <div class="header">
      <div class="selectNav">
        <el-select
          size="small"
          class="input_td"
          v-model="queryForm.dsj"
          clearable
          placeholder="请选择地市局"
          @change="gdjChange"
        >
          <el-option
            v-for="item in gdjOptions"
            :key="item.ORG_CODE"
            :label="item.ORG_NAME"
            :value="item.ORG_CODE"
          ></el-option>
        </el-select>
        <el-select
          v-model="queryForm.xqj"
          clearable
          placeholder="请选择县区局"
          class="input_td"
          size="small"
          @change="qxjChange"
        >
          <el-option
            v-for="item in xqjOptions"
            :key="item.ORG_CODE"
            :label="item.ORG_NAME"
            :value="item.ORG_CODE"
          ></el-option>
        </el-select>
        <el-select
          size="small"
          class="input_td"
          v-model="queryForm.gds"
          clearable
          placeholder="请选择供电所"
        >
          <el-option
            v-for="item in gdsOptions"
            :key="item.ORG_CODE"
            :label="item.ORG_NAME"
            :value="item.ORG_CODE"
          ></el-option>
        </el-select>
        <el-input
          class="input_300"
          placeholder="请输入用户编号"
          size="small"
          v-model="queryForm.yhbh"
          clearable
        ></el-input>
        <el-button type="primary" size="small" icon="el-icon-search" @click="getClinetData">查询</el-button>
      </div>
      <div class="selectBut">
        <el-button type="primary" size="small" @click="changeComponents(1)">第2次停电通知算法识别结果清单</el-button>
        <el-button type="primary" size="small" @click="changeComponents(2)">第2次停电通知客户统计分布查询</el-button>
        <el-button type="primary" size="small" @click="changeComponents(3)">第2次停电通知客户手工标注清单明细</el-button>
        <el-button type="primary" size="small" @click="changeComponents(4)">第2次停电通知客户手工维护</el-button>
      </div>
    </div>
    <div class="content">
      <div class="title">第2次停电通知的客户清单</div>
      <div class="tableTd">
        <el-table
          v-loading="loading"
          border
          :data="clinetData"
          style="width: 100%"
          :row-class-name="tableRowClassName"
        >
          <el-table-column label="序号" align="center" type="index" width="70"></el-table-column>
          <el-table-column prop="yhbh" label="用户编号"></el-table-column>
          <el-table-column prop="yhmc" label="用户名称"></el-table-column>
          <el-table-column prop="yddz" label="用电地址"></el-table-column>
          <el-table-column prop="dsj" label="地市局"></el-table-column>
          <el-table-column prop="xqj" label="县区局"></el-table-column>
          <el-table-column prop="gds" label="供电所"></el-table-column>
          <el-table-column prop="updateTime" label="标签更新时间"></el-table-column>
          <el-table-column prop="way" label="第二次短信发送的标记方式"></el-table-column>
        </el-table>
        <div class="block">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryForm.current"
            :page-sizes="[ 10, 20, 30, 40, 50]"
            :page-size="queryForm.size"
            layout="total, sizes, prev, pager, next, jumper"
            :total="queryForm.total"
          ></el-pagination>
        </div>
      </div>
    </div>
    <el-dialog
      :title="dialogTitle"
      fullscreen
      :visible.sync="dialogVisible"
      width="90%"
      :before-close="handleClose"
      v-if="dialogVisible"
    >
      <div class="dialogCon">
        <component :is="componentName"></component>
      </div>
      <span slot="footer" class="dialog-footer"></span>
    </el-dialog>
  </div>
</template>

<script>
import distributionCharts from "./complaintDiagnosis/distributionCharts";
import outPowerListDetailed from "./complaintDiagnosis/outPowerListDetailed";
import manualManage from "./complaintDiagnosis/manualManage";
import manualListDetails from "./complaintDiagnosis/manualListDetails";
import Interface1 from '@/base/mixin/power-sensitive-users';
import Interface2 from '@/base/mixin/common-api';

export default {
  name: "probablyComplaintUser",
  components: {
    distributionCharts,
    outPowerListDetailed,
    manualManage,
    manualListDetails,
  },
  mixins: [Interface1, Interface2],
  props: {

  },
  data() {
    return {
      //导航模块变量
      gdjOptions: [],
      xqjOptions: [],
      gdsOptions: [],
      queryForm: {
        // yhbh: "0313001300620313",
        yhbh: "",
        dsj: "",
        xqj: "",
        gds: "",
        current: 1,
        size: 10,
        total: 0,
      },
      //第2次停电通知的客户清单
      clinetData: [],

      loading: false,
      //弹窗组件
      dialogTitle: '',
      dialogVisible: false,
      componentName: '',
    }
  },
  mounted() {
    this.getAreaCode()
    this.getClinetData()
  },
  methods: {
    //地市局选择
    gdjChange() {
      this.queryForm.gds = ''
      this.gdsOptions = []
      this.queryForm.xqj = ''
      this.xqjOptions = []
      this.getQxjPowerAreaCode()
    },
    //区县局选择
    qxjChange() {
      this.queryForm.gds = ''
      this.gdsOptions = []
      this.getGdsPowerAreaCode()
    },
    handleSizeChange(val) {
      this.queryForm.size = val
      this.getClinetData()
    },
    handleCurrentChange(val) {
      this.queryForm.current = val
      this.getClinetData()
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 === 0) {
        return 'success-row';
      }
      return '';
    },
    changeComponents(v) {
      if (v === 1) {
        this.dialogTitle = '第2次停电通知算法识别结果清单'
        this.componentName = 'outPowerListDetailed'
        this.dialogVisible = true;
      } else if (v === 2) {
        this.dialogTitle = '第2次停电通知客户统计分布查询'
        this.componentName = 'distributionCharts'
        this.dialogVisible = true;
      } else if (v === 3) {
        this.dialogTitle = '第2次停电通知手工标注结果清单'
        this.componentName = 'manualListDetails'
        this.dialogVisible = true;
      } else if (v === 4) {
        this.dialogTitle = '第2次停电通知客户手工标注'
        this.componentName = 'manualManage'
        this.dialogVisible = true;
      }
    },
    handleClose(done) {
      done();
      // this.$confirm('确认关闭？')
      //   .then(_ => {
      //     done();
      //   })
      //   .catch(_ => {});
    },
    //获取供电单位编码--地市局
    getAreaCode() {
      this.getPowerAreaCode({
        "orgLev": "2",//2==供电单位==地市局，3==县区局局，4===供电所
        // "orgCode":"0306" //上一级编码
      }).then(res => {
        this.gdjOptions = res
      })
    },
    //获取区县局--区县局
    getQxjPowerAreaCode() {
      this.getPowerAreaCode({
        "orgLev": "3",//2==供电单位==地市局，3==县区局局，4===供电所
        "orgCode": this.queryForm.dsj //上一级编码
      }).then(res => {
        this.xqjOptions = res
      })
    },
    //获取供电所
    getGdsPowerAreaCode() {
      this.getPowerAreaCode({
        "orgLev": "4",//2==供电单位==地市局，3==县区局局，4===供电所
        "orgCode": this.queryForm.xqj //上一级编码
      }).then(res => {
        this.gdsOptions = res
      })
    },
    // 获取第2次停电通知的客户清单
    getClinetData() {
      this.getSecondTimeCustList(this.queryForm).then(res => {
        this.clinetData = [...res.records]
        this.queryForm.total = res.total
      })
    },
  }
}
</script>

<style scoped lang="scss">
.probablyComplaintUser {
  .input_td {
    width: 200px;
    margin-right: 20px;
  }
  .input_300 {
    width: 300px;
    margin-right: 20px;
  }
  .selectBut {
    .el-button {
      margin-right: 12px;
    }
  }
  .header {
    line-height: 30px;
    .selectNav {
      height: 30px;
      margin-bottom: 16px;
    }
    .selectBut {
      height: 30px;
      margin-bottom: 16px;
    }
  }
  .content {
    .title {
      padding-left: 18px;
      border-left: 6px solid #1a84f4;
      font-weight: bold;
      margin-bottom: 10px;
    }
    .tableTd {
      .block {
        text-align: center;
        margin-top: 10px;
      }
    }
  }
}
</style>